// src/common.ts
var i_min = -Infinity;
var max = Math.max;
function is_upper(index, str) {
  const code = str.charCodeAt(index);
  return 65 <= code && code <= 90;
}
function is_lower(index, str) {
  const code = str.charCodeAt(index);
  return 97 <= code && code <= 122;
}
function is_alnum(index, str) {
  const code = str.charCodeAt(index);
  return 97 <= code && code <= 122 || 65 <= code && code <= 90 || 48 <= code && code <= 57;
}
function compute_score(jump, first_char, match2, str) {
  const adjacency_bonus = 15;
  const separator_bonus = 30;
  const camel_bonus = 30;
  const first_letter_bonus = 15;
  const leading_letter_penalty = -5;
  const max_leading_letter_penalty = -15;
  let score = 0;
  if (!first_char && jump === 0) {
    score += adjacency_bonus;
  }
  if (!first_char || jump > 0) {
    str.charCodeAt(match2);
    if (is_upper(match2, str) && is_lower(match2 - 1, str)) {
      score += camel_bonus;
    }
    if (is_alnum(match2, str) && !is_alnum(match2 - 1, str)) {
      score += separator_bonus;
    }
  }
  if (first_char && jump === 0) {
    score += first_letter_bonus;
  }
  if (first_char) {
    score += max(leading_letter_penalty * jump, max_leading_letter_penalty);
  }
  return score;
}

// src/match.ts
function match(pattern, str) {
  const unmatched_letter_penalty = -1;
  const s_len = str.length;
  const p_len = pattern.length;
  let score = 100;
  if (p_len === 0) {
    return score;
  }
  if (s_len < p_len) {
    return i_min;
  }
  score += unmatched_letter_penalty * (s_len - p_len);
  score = match_recurse(
    0,
    p_len,
    0,
    pattern,
    str,
    str.toUpperCase(),
    score,
    true
  );
  return score;
}
function match_recurse(p_index, p_len, s_index, pattern, str, upper_str, score, first_char) {
  if (p_index === p_len) {
    return score;
  }
  let match2 = s_index - 1;
  let best_score = i_min;
  const search = pattern[p_index].toUpperCase();
  while ((match2 = upper_str.indexOf(search, match2 + 1)) !== -1) {
    const sub_score = match_recurse(
      p_index + 1,
      p_len,
      match2 + 1,
      pattern,
      str,
      upper_str,
      compute_score(match2 - s_index, first_char, match2, str),
      false
    );
    best_score = max(best_score, sub_score);
  }
  if (best_score === i_min) {
    return i_min;
  } else {
    return score + best_score;
  }
}

// src/trace.ts
var Trace = class {
  constructor(score, stops) {
    this.score = score;
    this.stops = stops;
  }
};
function match_trace(pattern, str) {
  const unmatched_letter_penalty = -1;
  const s_len = str.length;
  const p_len = pattern.length;
  let score = 100;
  if (p_len === 0) {
    return new Trace(score, []);
  }
  if (s_len < p_len) {
    return null;
  }
  score += unmatched_letter_penalty * (s_len - p_len);
  const trace = Array(pattern.length);
  score = trace_recurse(
    0,
    p_len,
    0,
    pattern,
    str,
    str.toUpperCase(),
    score,
    true,
    trace
  );
  if (score === i_min) {
    return null;
  }
  return new Trace(score, trace);
}
function trace_recurse(p_index, p_len, s_index, pattern, str, upper_str, score, first_char, trace) {
  if (p_index === p_len) {
    return score;
  }
  let match2 = s_index - 1;
  let best_score = i_min;
  const search = pattern[p_index].toUpperCase();
  while ((match2 = upper_str.indexOf(search, match2 + 1)) !== -1) {
    const sub_score = trace_recurse(
      p_index + 1,
      p_len,
      match2 + 1,
      pattern,
      str,
      upper_str,
      compute_score(match2 - s_index, first_char, match2, str),
      false,
      trace
    );
    if (best_score < sub_score) {
      best_score = sub_score;
      trace[p_index] = match2;
    }
  }
  if (best_score === i_min) {
    return i_min;
  } else {
    return score + best_score;
  }
}

export { Trace, match, match_trace };
